
import { defineComponent } from 'vue'

export default defineComponent({
    props : {
        label: {
            type: String,
            required: true
        },
        image: {
            type: String,
            required: true
        },
        isActive: {
            type: Boolean,
            default: true
        }
    },
    computed: {
        state(): string {
            return this.isActive ? 'active-card-color' : 'disabled-card-color'
        },
        icon(): string {
            return `assets/images/${this.image}.png`;
        }
    }
})
